@font-face {
  font-family: "Space";
  src: url(../../../../../public/assets/ethnocentric\ rg\ it.otf);
}
.hero {
  position: relative;
  height: 90vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  padding-top: 1rem;
  color: var(--white);
}
.title {
  font-size: 1.5rem;
  font-family: "Space", sans-serif;
}
.subtitle {
  font-weight: 400;
  font-size: 1.3rem;
  margin: 0.5rem 0;
}
.hero-btns {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  gap: 1rem;
}
.planet {
  position: absolute;
}
.planet-green {
  filter: drop-shadow(0 0 20px #00c32a);
  right: -30px;
  bottom: 60%;
}
.planet-earth {
  filter: drop-shadow(0 0 20px #2416e2);
  left: -80px;
  bottom: 40%;
}
.planet-pink {
  filter: drop-shadow(0 0 20px #f4317c);
  right: -80px;
  bottom: 15%;
  z-index: 2;
}
#planet-orange {
  filter: drop-shadow(0 0 100px #c43906);
}
#react-logo {
  filter: drop-shadow(0 0 20px #61dafb);
}
.hero-astronaut {
  position: absolute;
  bottom: -25%;
}

/**** Media Queries ****/
@media (prefers-reduced-motion: no-preference) {
  /***************
  ** Animations **
  ***************/
  .planet {
    animation: float 2s ease-in-out infinite;
    -webkit-animation: float 3s ease-in-out infinite; /* QQ Browser */
  }
  #js-logo {
    animation: glow 2s ease-in-out infinite;
  }
  .star {
    animation: twinkle 3s ease-in-out infinite;
    -webkit-animation: twinkle 3s ease-in-out infinite; /* QQ Browser */
  }
  .star {
    height: 5px;
    width: 5px;
    border-radius: 50%;
    background-color: var(--white);
    position: absolute;
    top: 50%;
    box-shadow: 0 0 10px var(--white);
    opacity: 0;
  }
  .star1 {
    top: 10%;
    left: 10%;
  }
  .star2 {
    top: 30%;
    right: 30%;
    animation-delay: 1s;
  }
  .star3 {
    bottom: 0%;
    left: 30%;
    animation-delay: 1.3s;
  }
  .star4 {
    bottom: 0%;
    right: 10%;
    animation-delay: 1.5s;
  }
  @keyframes glow {
    0% {
      filter: drop-shadow(0 0 20px #f5de19);
    }
    50% {
      filter: drop-shadow(0 0 90px #f5de19);
    }
    100% {
      filter: drop-shadow(0 0 20px #f5de19);
    }
  }
  @keyframes twinkle {
    0% {
      transform: scale(0);
      opacity: 0;
    }
    50% {
      transform: scale(1.1);
      opacity: 1;
    }
    100% {
      transform: scale(0);
      opacity: 0;
    }
  }
  @keyframes float {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(5px);
    }
    100% {
      transform: translateY(0);
    }
  }
}
@media screen and (min-width: 375px) {
  .hero {
    padding-top: 2rem;
  }
  .hero-astronaut {
    height: 450px;
    bottom: -20%;
  }
}
@media screen and (min-width: 390px) {
  .title {
    font-size: 2rem;
  }
  .subtitle {
    font-size: 1.5rem;
    margin: 1rem 0;
  }
  .hero-p,
  .hero-btns {
    font-size: 1.3rem;
  }
  .planet-green {
    height: 100px;
    right: 0;
  }
  .planet-earth {
    left: -40px;
  }
  .planet-pink {
    height: 150px;
  }
}
@media screen and (min-width: 768px) {
  .hero {
    padding-top: 4rem;
  }
  .title {
    font-size: 2.5rem;
  }
  .subtitle {
    font-size: 2rem;
    margin: 1rem 0;
  }
  .hero-btns {
    flex-direction: row;
  }
  .hero-p {
    font-size: 1.3rem;
  }
  .planet-green {
    right: 1rem;
  }
  .planet-earth {
    left: 1rem;
  }
  .planet-pink {
    right: 1rem;
  }
}
@media screen and (min-width: 1024px) {
  .planet-green {
    height: 150px;
    right: 2rem;
  }
  .planet-earth {
    left: 2rem;
    height: 180px;
  }
  .planet-pink {
    height: 200px;
    right: 2rem;
  }
  .hero-astronaut {
    height: 500px;
    bottom: -15%;
  }
}
@media screen and (min-width: 1280px) {
  .title {
    font-size: 3rem;
  }
  .subtitle {
    font-size: 2.5rem;
  }
  .hero-p {
    font-size: 1.5rem;
  }
  .planet-green {
    right: 20%;
  }
  .planet-earth {
    left: 10%;
    height: 300px;
  }
  .planet-pink {
    height: 350px;
    right: 2rem;
  }
  .hero-astronaut {
    height: 600px;
    bottom: -20%;
  }
}
@media screen and (min-width: 1536px) {
  .title {
    font-size: 4rem;
  }
  .subtitle {
    font-size: 3rem;
  }
  .hero-p {
    font-size: 2rem;
  }
  .planet-green {
    right: 20%;
  }
  .planet-earth {
    height: 350px;
  }
  .planet-pink {
    height: 400px;
  }
}
